import chroma from 'chroma-js';

// https://gka.github.io/chroma.js/

export class WaColor {
  readonly name: string;
  readonly yomi: string;
  readonly hex: string;
  // readonly color: Color;

  constructor(name: string, yomi: string, hex: string) {
    this.name = name;
    this.yomi = yomi;
    this.hex = chroma(hex).hex();
  }

  // 明るさ(0.0-1.0)を返す。
  luminance = (): number => {
    return chroma(this.hex).luminance();
  };

  // RGBのCSS表記を返す。
  cssRgb = (): string => {
    return chroma(this.hex).css();
  };

  // HSLのCSS表記を返す。
  cssHsl = (): string => {
    // chromaのデフォルト処理では小数点以下の数字が出てくるので、整数に丸める。
    let h = chroma(this.hex).get('hsl.h');
    let s = chroma(this.hex).get('hsl.s');
    let l = chroma(this.hex).get('hsl.l');

    h = Math.round(h);
    s = Math.round(s * 100.0); // 0..1が返ってくるので100倍して%化
    l = Math.round(l * 100.0); // 0..1が返ってくるので100倍して%化

    // return chroma(this.hex).css('hsl');
    return `hsl(${h},${s}%,${l}%)`;
  };

  isEqual = (other: WaColor): boolean => {
    if (this.name === other.name && this.hex === other.hex) {
      return true;
    }
    return false;
  };

  // 渡された文字列で色名を検索して返す。基本的に前方一致。
  // 見付からなかったら null を返す。
  static search = (word: string): WaColor | null => {
    for (const line of COLORS) {
      const a = line.split(',');

      // 漢字の名前で検索。
      if (a[0].match(`^${word}`) != null) {
        return new WaColor(a[0], a[1], a[2]);
      }

      // 読みで検索。
      if (a[1].match(`^${word}`) != null) {
        return new WaColor(a[0], a[1], a[2]);
      }
    }

    return null;
  };

  static randomColor = (): WaColor => {
    const s = COLORS[Math.floor(Math.random() * COLORS.length)];
    const a = s.split(',');
    return new WaColor(a[0], a[1], a[2]);
  };
}

